.home-container {
    text-align: center;
    background: url('C:\Users\lokes\OneDrive\Desktop\Projects\personalized-gift-store\public\images\theme.jpeg') no-repeat center center/cover;
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    color: white;
    font-family: 'Dancing Script', cursive;
  }
  
  .home-container h1 {
    font-size: 45px;
    text-shadow: 2px 2px 5px rgba(0, 0, 0, 0.5);
  }
  
  .home-container p {
    font-size: 22px;
    margin-top: 10px;
  }
  
  .explore-btn {
    margin-top: 20px;
    background-color: #62d1fa;
    color: white;
    border: none;
    padding: 12px 25px;
    font-size: 18px;
    border-radius: 25px;
    cursor: pointer;
    transition: 0.3s;
  }
  
  .explore-btn:hover {
    background-color: #e63956;
  }
  